





























import { Component, Vue } from 'vue-property-decorator';
import { Action, Getter, State } from 'vuex-class';
import { IArticle, ICommercialItem, IExamInfoListInput } from '@/types/interface';
import Banner from '../../components/Banner.vue';
import AdvertisingBannerList from '../../components/AdvertisingBannerList.vue';

@Component({
  components: {
    Banner,
    AdvertisingBannerList,
  },
})

export default class ExamInfoContent extends Vue {
  @State('commercialList') stateCommercialList!: ICommercialItem[];
  @Getter('examInfoById', { namespace: 'examInfo' }) examInfoById!: (id: number) => IArticle;
  @Getter('articleNotificationById', { namespace: 'notification' }) articleNotificationById!: (id: number) => IArticle;
  @Action('getExamInfoList', { namespace: 'examInfo' }) getExamInfoList!: (params: IExamInfoListInput) => Promise<void>;

  link() {
    if (this.isNotification) this.$router.push('/notification/main');
    else this.$router.go(-1);
  }

  get isNotification() {
    return this.$route.query.process === 'notification';
  }

  content: IArticle = {
    id: 0,
    title: '',
    url: '',
    imgUrl: '',
    subTitle: '',
    source: '',
    brief: '',
    hashtags: [],
    note: '',
  }

  created() {
    const {
      id,
    } = this.$route.query;
    const idInt = parseInt(id.toString(), 10);

    if (id) {
      this.content = this.isNotification ? this.articleNotificationById(idInt) : this.examInfoById(idInt);
    }
  }

  showShare() {
    return navigator.share;
  }

  share() {
    const shareData = {
      url: this.content.url,
      title: this.content.title,
      text: this.content.brief,
    };
    if (navigator.share) {
      navigator.share(shareData);
    }
  }

  selectTag(tag: string) {
    if (this.isNotification) return;
    this.getExamInfoList({
      isLongPeriod: true,
      searchKey: '',
      keyword: tag,
    })
    .then(() => {
      this.$router.push({ path: '/main/exam_info', query: { tag } });
    });
  }
}
