








import { Component, Vue, Prop } from 'vue-property-decorator';
import { ICommercialItem } from '@/types/interface';

@Component
export default class AdvertisingBannerList extends Vue {
  @Prop({ default: () => [] }) list!: ICommercialItem[];

  get evenList() {
    const temp = JSON.parse(JSON.stringify(this.list));
    if ((this.list.length % 2) !== 0) temp.splice(temp.length - 1, 1);
    return temp;
  }
}
